import React from 'react';

export class RegistrationForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            password2: '',
            email: '',
            first_name: '',
            last_name: '',
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        const target = event.target;
        const name = target.name;
        const value = target.value;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        fetch(`http://127.0.0.1:8000/api/v1/user/signup/`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state),
        }).then(resp => {
            if (resp.status == 201) {
                alert('Вы успешно зарегистрированы');
                window.location.href = "/files-storage-ui";
            } else {
                alert('Неудачная регистрация');
            }
        })

    }

    render() {
        return (
            <form onSubmit={this.handleSubmit} className={"center-form"}>
                Registration
                <input name="username" placeholder="username" type="text" value={this.state.username} onChange={this.handleChange} />
                <input name="password" placeholder="password" type="password" value={this.state.password} onChange={this.handleChange} />
                <input name="password2" placeholder="password2" type="password" value={this.state.password2} onChange={this.handleChange} />
                <input name="first_name" placeholder="first_name" type="text" value={this.state.first_name} onChange={this.handleChange} />
                <input name="last_name" placeholder="last_name" type="text" value={this.state.last_name} onChange={this.handleChange} />
                <input name="email" placeholder="email" type="email" value={this.state.email} onChange={this.handleChange} />
                <input type="submit" value="Отправить" className="submit_button"/>
            </form>
        );
    }
}