import React from 'react';
import Modal from '@material-ui/core/Modal';
import './modal.css'

export default function FilePropertyModal({data}) {
    const [open, setOpen] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <div onClick={handleOpen}>Get folder property</div>
            <Modal open={open} onClose={handleClose} className="modal">
                <div className="modal-main">
                    <h2>File Property</h2>
                    <p>
                        <span className="modal-span">File size:</span> {data.size} byte
                    </p>
                    <p>
                        <span className="modal-span">File owner:</span> {data.owner}
                    </p>
                </div>
            </Modal>
        </div>
    );
}
