import { Component } from 'react';
import './Forms/forms.css'

export class History extends Component {
    constructor(props) {
        super(props);
    }


    render() {
        var newDate = new Date(this.props.data.opened);


        return (<div style={{ display: "flex", alignItems: "center", marginTop: "10px"}}>
            <p style={{ margin: 0, marginRight: "10px" }}>{this.props.data.username}</p>
            <p style={{ margin: 0}}> {newDate.getDate()}.{newDate.getMonth()}.{newDate.getFullYear()} {newDate.getHours()}:{newDate.getMinutes()}</p>

        </div>);
    }
}

