import { Component } from 'react';
import {AppBar, IconButton, Toolbar, Avatar} from "@material-ui/core";
import {
    Link
} from "react-router-dom";

export class Header extends Component {
    constructor(props) {
        super(props);
    }

    logout = () => {
        localStorage.removeItem("token");
        window.location.href = "/files-storage-ui";
    }

    render() {
        let main;
        if (this.props.username) {
            main = <div style={{ display: "flex", alignItems: "center"}}>
                <div className="link" id="about" style={{ color: "#4285f4"}}>{this.props.username}</div>
                <Avatar alt="avatar" style={{ marginLeft: "5px", background: "#4285f4" }}>
                    {this.props.username.substr(0, 1)}
                </Avatar>
                <div style={{ marginLeft: "95px", cursor: "pointer", color: "red" }} onClick={this.logout}>
                    Exit
                </div>
            </div>
        } else  {
            main = <div>
                <Link className="link" to="/login">Login</Link>
                <Link className="link" to="/registration" style={{ color: "#4285f4" }}>Registration</Link>
            </div>
        }

        return <AppBar style={{ background: "none"}} position="static" elevation={0}>
                    <Toolbar style={{ color: "#4285f4", justifyContent: "flex-end" }}>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <div style={{ display: "flex", paddingTop: "12px"}}>
                                {main}
                            </div>
                        </div>
                    </Toolbar>
                </AppBar>
    }
}
