import { Component } from 'react';
import './Forms/forms.css'

export class UserShare extends Component {
    constructor(props) {
        super(props);
    }

    deleteUserShare = () => {
        fetch(`http://127.0.0.1:8000/api/v1/folder/share/${this.props.data.id}`, {
            method: 'DELETE',
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        })
        window.location.reload();
    }

    render() {
        return (<div style={{ display: "flex", alignItems: "center", marginTop: "10px"}}>
            <p style={{ margin: 0, marginRight: "10px" }}>{this.props.data.username}</p>
            <button className="submit_button" style={{ margin: 0, height: "25px" }}>Delete share</button>
        </div>);
    }
}

