import React from 'react';
import './forms.css'

export class CreateFolderForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {value: 'Noname'};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }

    handleSubmit(event) {
        fetch(`http://127.0.0.1:8000/api/v1/folder/${this.props.folder_id}`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
            body: JSON.stringify(
                {folder_name: this.state.value}
            ),
        })
        event.preventDefault();
        window.location.reload();
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <input type="text" value={this.state.value} onChange={this.handleChange} />
                <input type="submit" value="Отправить" className="submit_button"/>
            </form>
        );
    }
}