import { Component } from 'react';
import { Folder } from './Folder'
import { File } from './File'
import './folder.css'
import SimpleModal from './Modal/Modal'
import { ShareFolderForm } from './Forms/ShareFolderForm'
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import { UserShare } from './UserShare'
import { History } from './History'

export class FolderList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            folders: [],
            files: [],
            folderMembers: [],
            folderHistory: []
        };
    }

    getFolderItems = () => {
        fetch(`http://127.0.0.1:8000/api/v1/folder/${this.props.match.params.id}`, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        }).then(res => res.json())
            .then(json => {
                console.log(json);
                this.setState(
                    {
                        folders: json.folders,
                        files: json.files
                    }
                )
            });
        fetch(`http://127.0.0.1:8000/api/v1/folder/share/${this.props.match.params.id}`, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        }).then(res => res.json())
            .then(json => {
                console.log(json);
                this.setState(
                    {
                        folderMembers: json.members,
                    }
                )
            });
        fetch(`http://127.0.0.1:8000/api/v1/folder/${this.props.match.params.id}/history`, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        }).then(res => res.json())
            .then(json => {
                console.log(json);
                this.setState(
                    {
                        folderHistory: json.folder_history,
                    }
                )
            });
    }

    componentDidUpdate(prevProps) {
        if (this.props.match.params.id !== prevProps.match.params.id) {
            this.getFolderItems();
        }
    }

    componentDidMount() {
        this.getFolderItems();
    }

    onSelectImageHandler = (files) => {
        console.log(files)
        const file = files[0]
        const data = new FormData();
        data.append('file', file);
        data.append('name', file.name);
        data.append('folder', this.props.match.params.id);
        fetch("http://127.0.0.1:8000/api/v1/file/add/", {
            method: 'PUT',
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
            body: data
        })
        window.location.reload();
    }

    insertFolder = () => {
        if (localStorage.getItem('move_from')) {
            fetch("http://127.0.0.1:8000/api/v1/folder/move/", {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                },
                body: JSON.stringify(
                    {
                        from_folder: Number(localStorage.getItem('move_from')),
                        to_folder: this.props.match.params.id
                    }
                )
            })
            localStorage.removeItem("move_from");
            window.location.reload();
        } else {
            alert('Нет папки');
        }
    }

    insertCopyFile = () => {
        if (localStorage.getItem('copy_file')) {
            fetch("http://127.0.0.1:8000/api/v1/file/copy/", {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                },
                body: JSON.stringify(
                    {
                        file: Number(localStorage.getItem('copy_file')),
                        to_folder: this.props.match.params.id
                    }
                )
            })
            localStorage.removeItem("copy_file");
            window.location.reload();
        } else {
            alert('Нет файла для копирования');
        }
    }

    insertFile = () => {
        if (localStorage.getItem('move_from')) {
            fetch("http://127.0.0.1:8000/api/v1/file/move/", {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                },
                body: JSON.stringify(
                    {
                        file: Number(localStorage.getItem('move_file')),
                        to_folder: this.props.match.params.id
                    }
                )
            })
            localStorage.removeItem("move_from");
            window.location.reload();
        } else {
            alert('Нет папки');
        }
    }

    goBack = () => {
        window.history.back();
    }

    render() {
        return (<div style={{ paddingLeft: "20px", paddingRight: "20px" }}>
            <ContextMenuTrigger id='maincontext'>
                <div style={{ display: "flex", alignItems: "center" }}>
                    <ArrowBackIcon onClick={this.goBack} style={{ cursor: "pointer", fontSize: "50px"}}/>
                    <h1 style={{ marginLeft: "10px" }}>Folders</h1>
                </div>
                <div className={"file-flex"}>
                    {this.state.folders.map((item) => <Folder key={item.id} data={item}/>)}
                </div>
                <h1>Files</h1>
                <div className={"file-flex"}>
                    {this.state.files.map((item) => <File key={item.id} data={item}/>)}
                </div>
            </ContextMenuTrigger>
            <h1>Share this folder</h1>
            <ShareFolderForm folder_id={this.props.match.params.id}/>
            {this.state.folderMembers.map((item) => <UserShare key={item.id} data={item} />)}

            <h1>Folder History</h1>
            {this.state.folderHistory.map((item) => <History key={item.id} data={item} />)}

            <ContextMenu id='maincontext'>
                <MenuItem onClick={this.getFileProperty}>
                    <div className={'contextmenu-item'}>
                        <SimpleModal data={this.props.match.params.id}/>
                    </div>
                </MenuItem>
                <MenuItem onClick={this.handleClick}>
                    <div className={'contextmenu-item'}>
                        <label className="custom-file-upload">
                            <input type="file" style={{display: "none"}} onChange={(e) => this.onSelectImageHandler(e.target.files)}/>
                            Upload file
                        </label>
                    </div>
                </MenuItem>
                <MenuItem onClick={this.insertFolder}>
                    <div className={'contextmenu-item'}>
                        <label className="custom-file-upload">
                            Insert folder
                        </label>
                    </div>
                </MenuItem>
                <MenuItem onClick={this.insertCopyFile}>
                    <div className={'contextmenu-item'}>
                        <label className="custom-file-upload">
                            Insert copy file
                        </label>
                    </div>
                </MenuItem>
            </ContextMenu>
        </div>);
    }
}
