import React from 'react';

export class LoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        const target = event.target;
        const name = target.name;
        const value = target.value;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        fetch('http://127.0.0.1:8000/api/v1/user/jwt/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.state)
        }).then(resp => {
            if (resp.status == 200) {
                resp.json().then(function (data) {
                    localStorage.setItem('token', data.token);
                })
                window.location.href = "/files-storage-ui";
            } else {
                alert('Неудачная авторизация');
            }
        })
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit} className={"center-form"}>
                Log In
                <input name="username" placeholder="username" type="text" value={this.state.username} onChange={this.handleChange} />
                <input name="password" placeholder="password" type="password" value={this.state.password} onChange={this.handleChange} />
                <input type="submit" value="Отправить" className="submit_button"/>
            </form>
        );
    }
}