import { Component } from 'react';
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import FolderIcon from '@material-ui/icons/Folder';
import './folder.css'
import {
    Link
} from "react-router-dom";
import FolderPropertyModel from './Modal/FolderPropertyModal'

export class Folder extends Component {
    constructor(props) {
        super(props);
        this.state = {
            numberFiles: null,
            numberFolders: null
        }
    }

    deleteFolder = () => {
        fetch(`http://127.0.0.1:8000/api/v1/folder/${this.props.data.id}`, {
            method: 'DELETE',
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        })
        window.location.reload();
    }

    getFolderProperty = () => {
        fetch(`http://127.0.0.1:8000/api/v1/folder/${this.props.data.id}/property`, {
            method: 'GET',
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        }).then(res => res.json())
            .then(json => {
                this.setState(
                    {
                        numberFiles: json.number_of_files,
                        numberFolders: json.number_of_folders
                    }
                )
            })
    }

    moveFolder = () => {
        localStorage.setItem('move_from', this.props.data.id);
    }

    render() {
        return (<div>
            <ContextMenuTrigger id={`fcontextmenu_${this.props.data.id}`}>
                <Link to={`/folder/${this.props.data.id}`} className={"folder-item"}>
                    <FolderIcon style={{ height: "45px", padding: "0 16px" }}/>
                    {this.props.data.name}
                </Link>
            </ContextMenuTrigger>

            <ContextMenu id={`fcontextmenu_${this.props.data.id}`}>
                <MenuItem onClick={this.deleteFolder}>
                    <div className={'contextmenu-item'}>
                        Delete folder
                    </div>
                </MenuItem>
                <MenuItem onClick={this.moveFolder}>
                    <div className={'contextmenu-item'}>
                        Move folder
                    </div>
                </MenuItem>
                <MenuItem onClick={this.getFolderProperty}>
                    <div className={'contextmenu-item'}>
                        <FolderPropertyModel data={this.state}/>
                    </div>
                </MenuItem>
            </ContextMenu>
        </div>);
    }
}

