import React from 'react';
import './forms.css'

export class ShareFolderForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {value: ''};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }

    handleSubmit(event) {
        fetch(`http://127.0.0.1:8000/api/v1/folder/share/${this.props.folder_id}`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
            body: JSON.stringify(
                {username: this.state.value}
            ),
        }).then(res => res.json())
            .then(json => {
                if (json.response == true) {
                    alert('Пользователь добавлен')
                } else {
                    alert('Пользователь не добавлен')
                }
            });
        event.preventDefault();

    }

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <input type="text" placeholder="USERNAME" value={this.state.value} onChange={this.handleChange} />
                <input type="submit" value="Отправить" className="submit_button"/>
            </form>
        );
    }
}