import { Component } from 'react';
import AssignmentIcon from '@material-ui/icons/Assignment';
import './folder.css'
import axios from "axios";
import { ContextMenu, MenuItem, ContextMenuTrigger } from "react-contextmenu";
import FilePropertyModel from './Modal/FilePropertyModal'

export class File extends Component {
    constructor(props) {
        super(props);
        this.state = {
            size: null,
            owner: null
        };
    }

    getFileProperty = () => {
        fetch(`http://127.0.0.1:8000/api/v1/file/${this.props.data.id}/property`, {
            method: 'get',
            mode: 'cors',
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        }).then(res => res.json())
            .then(json => {
                this.setState(
                    {
                        size: json.size,
                        owner: json.owner.username
                    }
                )
            })
    }

    downloadFile = () => {
        axios({
            url: `http://127.0.0.1:8000/api/v1/file/${this.props.data.id}`,
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
            method: 'GET',
            responseType: 'blob',
        }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', this.props.data.name);
            document.body.appendChild(link);
            link.click();
        });
    }

    deleteFile = () => {
        fetch(`http://127.0.0.1:8000/api/v1/file/${this.props.data.id}`, {
            method: 'DELETE',
            headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`
            },
        })
        window.location.reload();
    }

    copyFile = () => {
        localStorage.setItem('copy_file', this.props.data.id);
    }

    render() {
        return (<div>
            <ContextMenuTrigger id={`ficontextmenu_${this.props.data.id}`}>
                <div className={"folder-item"} id={this.props.data.id} onClick={this.downloadFile}>
                    <AssignmentIcon style={{ height: "45px", padding: "0 16px" }}/>
                    {this.props.data.name}
                </div>
            </ContextMenuTrigger>

            <ContextMenu id={`ficontextmenu_${this.props.data.id}`}>
                <MenuItem onClick={this.getFileProperty}>
                    <div className={'contextmenu-item'}>
                        <FilePropertyModel data={this.state}/>
                    </div>
                </MenuItem>
                <MenuItem onClick={this.copyFile}>
                    <div className={'contextmenu-item'}>
                        Copy file
                    </div>
                </MenuItem>
                <MenuItem onClick={this.deleteFile}>
                    <div className={'contextmenu-item'}>
                        Delete file
                    </div>
                </MenuItem>
            </ContextMenu>
        </div>);
    }
}