import React from "react";
import { Header } from "./components/Header/Header";
import { FolderList } from "./components/FolderList";
import { LoginForm } from "./components/Forms/LoginForm";
import { RegistrationForm } from './components/Forms/RegistrationForm'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";

export class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            logged_in: !!localStorage.getItem('token'),
            user_id: null,
            username: null,
            user_folder: null
        };
    }

    componentDidMount() {
        if (this.state.logged_in) {
            fetch('http://127.0.0.1:8000/api/v1/user/', {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                }
            })
                .then(res => res.json())
                .then(json => {
                    this.setState({
                        username: json.user_data.username,
                        user_id: json.user_data.id,
                        user_folder: "/folder/" + json.user_folder
                    });
                });
        }
    }

    // handle_login = (e, data) => {
    //     e.preventDefault();
    //     fetch('http://127.0.0.1:8000/api/v1/user/jwt/', {
    //         method: 'POST',
    //         headers: {
    //             'Content-Type': 'application/json'
    //         },
    //         body: JSON.stringify(data)
    //     })
    //         .then(res => res.json())
    //         .then(json => {
    //             if (json.token) {
    //                 console.log(json.token)
    //                 localStorage.setItem('token', json.token);
    //             } else {
    //                 alert("Некорректные данные");
    //             }
    //         });
    // };

    render() {

        return <div style={{minHeight: "100vh", display: "flex", flexFlow: "column"}}>
            <Router>
                <Header username={this.state.username} />
                <Switch>
                    <Route path="/folder/:id" component={FolderList} />
                    <Route path='/login'>
                        <LoginForm />
                    </Route>
                    <Route path='/registration'>
                        <RegistrationForm />
                    </Route>
                    <Route path="/">
                        { this.state.logged_in ? <Redirect to={this.state.user_folder} /> : <Redirect to="/login" />}
                    </Route>
                </Switch>
            </Router>

        </div>;
    }
}
